body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  margin: 0;
  padding: 0;

}

.App {
  background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url("https://www.rushhourdaily.com/wp-content/uploads/2020/02/gambling-2001078_1280.jpg"), no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.Header{display:flex;background:linear-gradient(rgba(0,0,0,0.7), rgba(0,0,0,0.7));border-bottom:1px solid white;justify-content:center}.Header__link{text-decoration:none;color:white;font-size:1.1rem}.Header__link:hover{text-shadow:0 0 5px white}.Header__link_active{text-shadow:0 0 9px white;font-weight:bold}.Header__list{width:70vw;list-style:none;display:flex;justify-content:space-between;align-items:center;margin:0;padding:0}.Header__list_logo{width:40px;height:40px;transition:height 0.4s, width 0.4s}.Header__list_logo:hover{width:50px;height:50px}.Header__list_logo_wrapper{width:50px;height:50px;overflow:hidden;display:flex;justify-content:center;align-items:center}.Header__list_item{text-align:center;font-family:'MuseoModerno', cursive;padding:0 10px;transition:text-shadow 0.3s}.Header__list_item:last-child{margin-right:0}@media screen and (max-width: 500px){.Header__list{width:100%}}

.MainPage{max-width:1200px;margin:0 auto;display:flex;flex-wrap:wrap;justify-content:center}

.GameCard{border-radius:20%;max-width:200px;max-height:200px;overflow:hidden;position:relative;display:flex;justify-content:center;align-items:center;margin:25px}.GameCard__image{transition:transform 0.5s;width:200px;height:200px}.GameCard__btn{position:absolute;text-align:center;bottom:0;left:0;width:100%;height:20%;background:white;font-family:'MuseoModerno', cursive;color:black;text-decoration:none;font-weight:bold;font-size:1.5em;border:none;margin-bottom:15%;transition:opacity 0.5s, background-color 0.5s, color 0.5s}.GameCard__btn:hover{cursor:pointer}.GameCard__btn:focus{outline:none;background:crimson;color:white}

.Footer{display:flex;max-width:1200px;justify-content:center;align-items:center;padding:0 30px;margin:0 auto}.Footer_wrapper{background:linear-gradient(rgba(0,0,0,0.7), rgba(0,0,0,0.7));width:100%}.Footer__list{padding:0;list-style:none;text-align:center}.Footer__list_active{text-shadow:0 0 9px white;font-weight:bold}.Footer__list_item{font-family:'MuseoModerno', cursive;transition:text-shadow 0.3s;white-space:nowrap;padding-bottom:10px;position:relative}.Footer__img{width:50px;height:50px;margin:0 30px}.Footer__link{text-decoration:none;color:white;position:relative}.Footer__link::after{content:"";background:white;mix-blend-mode:exclusion;width:calc(100% + 20px);height:0;position:absolute;bottom:-4px;left:-10px;transition:all 0.3s cubic-bezier(0.445, 0.05, 0.55, 0.95)}.Footer__link:hover::after{height:calc(100% + 8px)}.Footer__link_active{font-weight:bold}.Footer__link_active::after{content:"";height:calc(100% + 8px);background:white;mix-blend-mode:exclusion;position:absolute;bottom:-4px;left:-10px}.Footer__article_text{color:#f9bfb7}@media screen and (max-width: 700px){.Footer__article_text{font-size:2vmin}.Footer{flex-direction:column;text-align:center}}

.Information{max-width:1200px;margin:0 auto;padding:30px;font-family:'MuseoModerno', cursive;color:white;background:linear-gradient(rgba(0,0,0,0.7), rgba(0,0,0,0.7))}.Information__header{margin-top:0;padding-top:20px}.Information__last{margin-bottom:0;padding-bottom:20px}

.GameWindow{max-width:880px;margin:30px auto;text-align:center;color:white;font-size:larger;font-family:'MuseoModerno', cursive}

.Preloader{display:flex;flex-direction:column;justify-content:center;align-items:center;font-family:Arial, sans-serif;font-style:italic}.Preloader__header{text-align:center;color:white;font-size:3em;padding-top:30vh}.Preloader__btn{width:20vw;height:5vw;border-radius:5vw;background-color:#8ff9a4;cursor:pointer;font-style:italic;font-size:2vw;text-transform:uppercase;font-weight:bold;margin-bottom:30vh}.Preloader__btn:active{outline:none;background-color:#2c5336;color:white}@media screen and (max-width: 570px){.Preloader__btn{width:90%;height:15vw;font-size:5vmin}}

